@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

a {
  text-decoration: none;
  color: black;
}

.dark {
  color: #333333;
}

hr,
.m-none {
  border-style: solid;
  border-color: #bdbdbd;
  margin: 0;
}
input {
  outline: none;
}
ul {
  list-style-type: none;
  padding: 0;
}
.d-flex {
  display: flex;
}



footer {
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 50px;
  color: #a9a9a9;
}

footer a {
  color: #a9a9a9;
}

header {
  display: flex;
  justify-content: center;
  padding-top: 1rem;
  font-family: 'Raleway', sans-serif;
}

.tabs--button {
  min-width: 120px;
  min-height: 20px;
  background: none;
  border: none;
  outline: none;
  cursor: pointer;
  padding-bottom: 15px;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
}
.tabs--underline {
  border: none;
  border-bottom: 4px solid #6da6f2;
  border-radius: 4px 4px 0px 0px;
  outline: none;
}

.tabsList--container {
  display: flex;
  justify-content: space-evenly;
}

.todoAdd--container {
  padding-top: 0.5rem;
}
.todoAdd--container {
  display: flex;
}

.todoAdd--container input[type='text'] {
  width: 100%;
  margin-right: 25px;
  border: 1px solid #bdbdbd;
  box-sizing: border-box;
  border-radius: 12px;
  padding-left: 12px;
  color: #828282;
  font-family: 'Montserrat', sans-serif;
}

.todoAdd--container input[type='submit'] {
  width: 109px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  box-sizing: border-box;
  border-radius: 12px;
  border: none;
  background: #6da6f2;
  font-family: 'Montserrat', sans-serif;
  font-weight: 600;
  color: #fff;
  cursor: pointer;
  transition: 0.3s;
}

.todoAdd--container input[type='submit']:hover {
  background-color: #4e98fb;
}
.todoAdd--container input[type='text']:focus {
  border: 2px solid #6da6f2;
}

.todoPage--container {
  width: 60%;
  margin: auto;
}
@media (max-width: 500px) {
  .todoPage--container {
    width: 90%;
  }
}

.checkbox--container .checkmark {
  border: 1px solid #828282;
  border-radius: 4px;
  box-sizing: border-box;
  height: 24px;
  width: 24px;
  fill: white;
  cursor: pointer;
  transition: 0.3s;
}
.checkbox--container .checked {
  border-color: #6da6f2;
  background-color: #6da6f2;
  fill: white;
}

.todoItem--container {
  display: flex;
  align-items: center;
  cursor: pointer;
  margin-bottom: 8px;
  position: relative;
  justify-content: space-between;
  -webkit-animation: fadeIn 0.8s;
          animation: fadeIn 0.8s;
}

.todoItem--container span {
  margin-left: 8px;
  font-size: 18px;
  font-family: 'Montserrat', sans-serif;
}

.done-Item {
  text-decoration: line-through;
}
.delete--button {
  fill: #bdbdbd;
  transition: 0.3s;
}
.delete--button:hover {
  fill: #f18989;
}

@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 100%;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 100%;
  }
}

.Button--container {
  display: flex;
  justify-content: center;
  align-items: center;
  font-family: 'Montserrat', sans-serif;
  border-radius: 4px;
  color: #fff;
  padding: 12px;
  outline: none;
  fill: #fff;
  cursor: pointer;
  margin-left: auto;
}

.danger {
  background-color: #f18989;
  border: 1px solid #f18989;
  transition: 0.3s;
}
.danger:hover {
  background-color: #f74545;
  border: 1px solid #f74545;
}

